import React from "react";

import { fireEvent, render, screen } from "@testing-library/react";

import ButtonForBrand from "../ButtonForBrand";

describe("The Button component", () => {
	test("should render properly with text only.", () => {
		const handleClick = jest.fn(() => {});
		render(
			<ButtonForBrand
				dataQA="button-brand"
				brandIconText="Button"
				type="button"
				onClick={handleClick}
			/>,
		);

		const brandButton = screen.getByTestId("button-brand");
		expect(brandButton).toBeInTheDocument();
	});

	test("should render properly with image.", () => {
		const handleClick = jest.fn(() => {});
		render(
			<ButtonForBrand
				dataQA="button-brand"
				brandIconImage="./brand.png"
				type="button"
				onClick={handleClick}
			/>,
		);

		const brandButtonImage = screen.getByTestId("button-brand-image");
		expect(brandButtonImage).toBeInTheDocument();
	});

	test("should fire an event when clicked.", () => {
		const handleClick = jest.fn(() => {});
		render(
			<ButtonForBrand
				dataQA="button-brand"
				brandIconText="Button"
				type="button"
				onClick={handleClick}
			/>,
		);
		fireEvent.click(screen.getByTestId("button-brand"));
		expect(handleClick).toBeCalled();
	});
});
